@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@700&family=Raleway:wght@600&display=swap');
@font-face {
    font-family: chineseFont;
    src: url('../../styles/fonts/SentyWEN2017.ttf');
}

#home-body-container {
    padding-top: 6vw;
    padding-bottom: 2.5rem;
    display: flex;
    flex-direction: row;
    justify-content: center;
    background-color: white;
    gap: 4rem;
}

.home-body-item {
    display: flex;
    flex-direction: column;
    width:20rem;
    min-height: 34rem;
    align-items: center;
    justify-items: center;
    text-align: center;
}

.home-body-image {
    border-radius: 50%;
}

.home-body-title {
    padding: 1rem;
    display: flex;
    align-items: center;
    font-size: 1.6rem;
    height: 3.5rem;
    font-weight: bold;
    color:  darkred;
    font-family: 'Mulish', sans-serif;
}

.home-body-title.chinese {
    font-family: chineseFont;
}

.home-body-text {
    padding-top: 1rem;
    font-weight: bold;
    font-size: 1.4rem;
    color: darkblue;
    font-family: 'Mulish', sans-serif;
}

/* Laptop */
@media (max-width:1030px) { 
    #home-body-container {
        padding-top: 1rem;
        flex-direction: column;
        align-items: center;
        gap: 6vw
    }

    .home-body-item {
        display: grid;
        gap: 0.5rem;
        grid-template-columns: 1fr 5fr;
        width: 90vw;
        min-height: unset;        
    }

    .home-body-image {
        width: 34vw;
        grid-row: 1/3;
    }

    .home-body-title {
        font-size: 4vw;
    }

    .home-body-text {
        font-size: 2.5vw;
    }

}

/* mobile portrait */
@media (max-width:500px) { 
    #home-body-container {
        padding-top: 1rem;
        flex-direction: column;
        align-items: center;
        gap: 1rem;
    }

    .home-body-item {
        width: 90vw;
        min-height:unset;
        display: flex;
    }

    .home-body-image {
        width: unset;
    }


    .home-body-title {
        font-size: 8vw;
        height: unset;
        padding: unset;
    }

    .home-body-text {
        padding-top: 0rem;
        font-size: 5.4vw;
    }
}